"use server"

import { prisma } from "@/lib/prisma"
import {
  tsTypeToPrisma,
  formatTransaction,
  calculateMonthFromDate,
  prismaTypeToTS,
} from "@/lib/api-utils"
import { z } from "zod"
import { unstable_cache } from "next/cache"
import { revalidateTag } from "next/cache"

// Cache tags
const CACHE_TAG_TRANSACTIONS = "transactions"
const CACHE_TAG_TRANSACTION = (id: string) => `transaction-${id}`
const CACHE_TAG_STATS = "transaction-stats"

// Validation schemas
const createTransactionSchema = z.object({
  title: z.string().min(1, "Title is required"),
  amount: z.number().positive("Amount must be positive"),
  date: z
    .string()
    .regex(/^\d{4}-\d{2}-\d{2}$/, "Date must be in YYYY-MM-DD format"),
  category: z.string().min(1, "Category is required"),
  type: z.enum(["income", "expense", "savings"]),
})

const updateTransactionSchema = z.object({
  title: z.string().min(1, "Title is required").optional(),
  amount: z.number().positive("Amount must be positive").optional(),
  date: z
    .string()
    .regex(/^\d{4}-\d{2}-\d{2}$/, "Date must be in YYYY-MM-DD format")
    .optional(),
  category: z.string().min(1, "Category is required").optional(),
  type: z.enum(["income", "expense", "savings"]).optional(),
})

// Helper function to get transactions with caching
async function getTransactionsFromDB(filters: {
  type?: "income" | "expense" | "savings"
  month?: string
  category?: string
}) {
  const where: any = {}

  if (filters.type && ["income", "expense", "savings"].includes(filters.type)) {
    where.type = tsTypeToPrisma(filters.type)
  }

  if (filters.month) {
    where.month = filters.month
  }

  if (filters.category) {
    where.category = filters.category
  }

  const transactions = await prisma.transaction.findMany({
    where,
    orderBy: {
      date: "desc",
    },
  })

  return transactions.map(formatTransaction)
}

// Server Action: Get all transactions with optional filters
export async function getTransactions(filters?: {
  type?: "income" | "expense" | "savings"
  month?: string
  category?: string
}) {
  try {
    const cacheKey = `transactions-${JSON.stringify(filters || {})}`
    
    const cachedTransactions = unstable_cache(
      async () => getTransactionsFromDB(filters || {}),
      [cacheKey],
      {
        tags: [CACHE_TAG_TRANSACTIONS],
        revalidate: 3600, // Revalidate after 1 hour
      }
    )

    const transactions = await cachedTransactions()

    return {
      success: true as const,
      data: transactions,
    }
  } catch (error) {
    console.error("Error fetching transactions:", error)
    return {
      success: false as const,
      error: "Failed to fetch transactions",
      message: error instanceof Error ? error.message : "Unknown error",
    }
  }
}

// Server Action: Get a single transaction
export async function getTransaction(id: string) {
  try {
    const cachedTransaction = unstable_cache(
      async () => {
        const transaction = await prisma.transaction.findUnique({
          where: { id },
        })

        if (!transaction) {
          return null
        }

        return formatTransaction(transaction)
      },
      [`transaction-${id}`],
      {
        tags: [CACHE_TAG_TRANSACTION(id), CACHE_TAG_TRANSACTIONS],
        revalidate: 3600,
      }
    )

    const transaction = await cachedTransaction()

    if (!transaction) {
      return {
        success: false as const,
        error: "Transaction not found",
      }
    }

    return {
      success: true as const,
      data: transaction,
    }
  } catch (error) {
    console.error("Error fetching transaction:", error)
    return {
      success: false as const,
      error: "Failed to fetch transaction",
      message: error instanceof Error ? error.message : "Unknown error",
    }
  }
}

// Server Action: Create a new transaction
export async function createTransaction(
  data: z.infer<typeof createTransactionSchema>
) {
  try {
    // Validate request body
    const validationResult = createTransactionSchema.safeParse(data)
    if (!validationResult.success) {
      return {
        success: false as const,
        error: "Validation failed",
        details: validationResult.error.errors,
      }
    }

    const { title, amount, date, category, type } = validationResult.data

    // Calculate month from date
    const month = calculateMonthFromDate(date)

    // Create transaction in database
    const transaction = await prisma.transaction.create({
      data: {
        title,
        amount,
        date: new Date(date),
        category,
        type: tsTypeToPrisma(type),
        month,
      },
    })

    // Revalidate cache immediately
    revalidateTag(CACHE_TAG_TRANSACTIONS)
    revalidateTag(CACHE_TAG_STATS)
    revalidateTag(CACHE_TAG_TRANSACTION(transaction.id))

    return {
      success: true as const,
      data: formatTransaction(transaction),
    }
  } catch (error) {
    console.error("Error creating transaction:", error)
    return {
      success: false as const,
      error: "Failed to create transaction",
      message: error instanceof Error ? error.message : "Unknown error",
    }
  }
}

// Server Action: Update a transaction
export async function updateTransaction(
  id: string,
  data: Partial<z.infer<typeof updateTransactionSchema>>
) {
  try {
    // Check if transaction exists
    const existingTransaction = await prisma.transaction.findUnique({
      where: { id },
    })

    if (!existingTransaction) {
      return {
        success: false as const,
        error: "Transaction not found",
      }
    }

    // Validate request body
    const validationResult = updateTransactionSchema.safeParse(data)
    if (!validationResult.success) {
      return {
        success: false as const,
        error: "Validation failed",
        details: validationResult.error.errors,
      }
    }

    const updateData: any = {}

    if (validationResult.data.title !== undefined) {
      updateData.title = validationResult.data.title
    }
    if (validationResult.data.amount !== undefined) {
      updateData.amount = validationResult.data.amount
    }
    if (validationResult.data.date !== undefined) {
      updateData.date = new Date(validationResult.data.date)
      updateData.month = calculateMonthFromDate(validationResult.data.date)
    }
    if (validationResult.data.category !== undefined) {
      updateData.category = validationResult.data.category
    }
    if (validationResult.data.type !== undefined) {
      updateData.type = tsTypeToPrisma(validationResult.data.type)
    }

    const transaction = await prisma.transaction.update({
      where: { id },
      data: updateData,
    })

    // Revalidate cache immediately
    revalidateTag(CACHE_TAG_TRANSACTIONS)
    revalidateTag(CACHE_TAG_STATS)
    revalidateTag(CACHE_TAG_TRANSACTION(id))

    return {
      success: true as const,
      data: formatTransaction(transaction),
    }
  } catch (error) {
    console.error("Error updating transaction:", error)
    return {
      success: false as const,
      error: "Failed to update transaction",
      message: error instanceof Error ? error.message : "Unknown error",
    }
  }
}

// Server Action: Delete a transaction
export async function deleteTransaction(id: string) {
  try {
    // Check if transaction exists
    const existingTransaction = await prisma.transaction.findUnique({
      where: { id },
    })

    if (!existingTransaction) {
      return {
        success: false as const,
        error: "Transaction not found",
      }
    }

    await prisma.transaction.delete({
      where: { id },
    })

    // Revalidate cache immediately
    revalidateTag(CACHE_TAG_TRANSACTIONS)
    revalidateTag(CACHE_TAG_STATS)
    revalidateTag(CACHE_TAG_TRANSACTION(id))

    return {
      success: true as const,
      message: "Transaction deleted successfully",
    }
  } catch (error) {
    console.error("Error deleting transaction:", error)
    return {
      success: false as const,
      error: "Failed to delete transaction",
      message: error instanceof Error ? error.message : "Unknown error",
    }
  }
}

// Helper function to get statistics with caching
async function getStatsFromDB(month?: string) {
  const where: any = {}

  if (month) {
    where.month = month
  }

  const transactions = await prisma.transaction.findMany({
    where,
  })

  // Calculate totals
  let income = 0
  let expense = 0
  let savings = 0

  transactions.forEach((transaction) => {
    const amount = Number(transaction.amount)
    switch (transaction.type) {
      case "INCOME":
        income += amount
        break
      case "EXPENSE":
        expense += amount
        break
      case "SAVINGS":
        savings += amount
        break
    }
  })

  // Calculate additional statistics
  const totalTransactions = transactions.length
  const incomeTransactions = transactions.filter((t) => t.type === "INCOME").length
  const expenseTransactions = transactions.filter((t) => t.type === "EXPENSE").length
  const savingsTransactions = transactions.filter((t) => t.type === "SAVINGS").length

  // Calculate balance (income - expense - savings)
  const balance = income - expense - savings

  // Get category breakdown
  const categoryBreakdown: Record<string, { income: number; expense: number; savings: number }> = {}

  transactions.forEach((transaction) => {
    const category = transaction.category
    const amount = Number(transaction.amount)

    if (!categoryBreakdown[category]) {
      categoryBreakdown[category] = { income: 0, expense: 0, savings: 0 }
    }

    switch (transaction.type) {
      case "INCOME":
        categoryBreakdown[category].income += amount
        break
      case "EXPENSE":
        categoryBreakdown[category].expense += amount
        break
      case "SAVINGS":
        categoryBreakdown[category].savings += amount
        break
    }
  })

  return {
    totals: {
      income,
      expense,
      savings,
      balance,
    },
    counts: {
      total: totalTransactions,
      income: incomeTransactions,
      expense: expenseTransactions,
      savings: savingsTransactions,
    },
    categoryBreakdown,
  }
}

// Server Action: Get statistics/summary
export async function getTransactionStats(month?: string) {
  try {
    const cacheKey = `transaction-stats-${month || "all"}`
    
    const cachedStats = unstable_cache(
      async () => getStatsFromDB(month),
      [cacheKey],
      {
        tags: [CACHE_TAG_STATS, CACHE_TAG_TRANSACTIONS],
        revalidate: 3600,
      }
    )

    const stats = await cachedStats()

    return {
      success: true as const,
      data: stats,
    }
  } catch (error) {
    console.error("Error fetching statistics:", error)
    return {
      success: false as const,
      error: "Failed to fetch statistics",
      message: error instanceof Error ? error.message : "Unknown error",
    }
  }
}

